<template>
  <div>
    <div class="container">
      <h1 class="text-center mt-3">Stock:{{ filterData.length }}</h1>
      <nav class="navbar justify-content-center py-5">
        <form class="form-inline">
          <input
            class="form-control mr-sm-2"
            type="search"
            placeholder="Search"
            aria-label="Search"
            v-model="search"
          >
        </form>
      </nav>
      <button class="btn btn-outline-primary mb-3" @click="openModel(true)">新增</button>
      <div class="table-responsive-xl">
        <table class="table table-hover">
          <thead>
            <tr>
              <th scope="col">ID</th>
              <th scope="col">產品名稱</th>
              <th scope="col">Net</th>
              <th scope="col">規格</th>
              <th scope="col">倉庫位置</th>
              <th scope="col" class="text-center">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in filterData" :key="item.uid">
              <td class="font-weight-bold">{{ item.id }}</td>
              <td class="font-weight-bold">{{ item.Item }}</td>
              <td class="font-weight-bold">{{ item.Net }}</td>
              <td class="font-weight-bold">{{ item.spec }}</td>
              <td class="font-weight-bold">
                <div v-if="item.A">A ：{{ item.A }}</div>
                <div v-if="item.B1">
                  B1 ：{{ item.B1 }}
                  <span
                    v-if="item.B1_expery_date"
                    class="text-danger"
                  >--{{ item.B1_expery_date }}</span>
                </div>
                <div v-if="item.B2">
                  B2 ：{{ item.B2 }}
                  <span
                    v-if="item.B2_expery_date"
                    class="text-danger"
                  >--{{ item.B2_expery_date }}</span>
                </div>
                <div v-if="item.B3">
                  B3 ：{{ item.B3 }}
                  <span
                    v-if="item.B3_expery_date"
                    class="text-danger"
                  >--{{ item.B3_expery_date }}</span>
                </div>
              </td>
              <td width="119">
                <div class="text-center">
                  <button class="btn btn-outline-info mr-2" @click="openModel(false,item)">
                    <i class="fas fa-edit"></i>
                  </button>
                  <button class="btn btn-outline-danger" @click="openModel(null,item)">
                    <i class="fas fa-trash-alt"></i>
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <!-- Modal -->
    <div
      class="modal fade"
      id="dataModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" v-if="isnew === true">新增</h5>
            <h5 class="modal-title" v-else>修改</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-row">
              <div class="col-6 mb-3">
                <label for="id">ID</label>
                <input
                  type="text"
                  class="form-control"
                  id="id"
                  placeholder="id"
                  v-model="tempData.id"
                >
              </div>
              <div class="col-6 mb-3">
                <label for="Item">產品名稱</label>
                <input
                  type="text"
                  class="form-control"
                  id="Item"
                  placeholder="Item"
                  v-model="tempData.Item"
                >
              </div>
            </div>
            <div class="form-row">
              <div class="col-6 mb-3">
                <label for="Net">Net</label>
                <input
                  type="text"
                  class="form-control"
                  id="Net"
                  placeholder="Net"
                  v-model="tempData.Net"
                >
              </div>
              <div class="col-6 mb-3">
                <label for="spec">規格</label>
                <input
                  type="text"
                  class="form-control"
                  id="spec"
                  placeholder="spec"
                  v-model="tempData.spec"
                >
              </div>
            </div>
            <div class="form-row">
              <div class="col-6 mb-3">
                <label for="A">A 倉庫</label>
                <input type="text" class="form-control" id="A" v-model="tempData.A">
              </div>
            </div>
            <div class="form-row">
              <div class="col-6 mb-3">
                <label for="B1">B1 倉庫</label>
                <input type="text" class="form-control" id="B1" v-model="tempData.B1">
              </div>
              <div class="col-6 mb-3">
                <label for="B1_expery_date">日期</label>
                <input
                  type="text"
                  class="form-control"
                  id="B1_expery_date"
                  v-model="tempData.B1_expery_date"
                >
              </div>
            </div>
            <div class="form-row">
              <div class="col-6 mb-3">
                <label for="B2">B2 倉庫</label>
                <input type="text" class="form-control" id="B2" v-model="tempData.B2">
              </div>
              <div class="col-6 mb-3">
                <label for="B2_expery_date">日期</label>
                <input
                  type="text"
                  class="form-control"
                  id="B2_expery_date"
                  v-model="tempData.B2_expery_date"
                >
              </div>
            </div>
            <div class="form-row">
              <div class="col-6 mb-3">
                <label for="B3">B3 倉庫</label>
                <input type="text" class="form-control" id="B3" v-model="tempData.B3">
              </div>
              <div class="col-6 mb-3">
                <label for="B3_expery_date">日期</label>
                <input
                  type="text"
                  class="form-control"
                  id="B3_expery_date"
                  v-model="tempData.B3_expery_date"
                >
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updataData">儲存</button>
          </div>
        </div>
      </div>
    </div>
    <!-- deleteModal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="deleteModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-danger">
            <h5 class="modal-title text-white" id="deleteModalLabel">確定要刪除嗎?</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body text-center">
            <h1>{{ tempData.Item }}</h1>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary mr-auto" data-dismiss="modal">取消</button>
            <button type="button " class="btn btn-danger" @click="deleteData(tempData.uid)">刪除</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      excel: [],
      excel_data: [],
      tempData: {},
      search: "",
      isnew: true
    };
  },
  computed: {
    filterData() {
      return this.excel_data.filter(item => {
        return (
          item.id.indexOf(this.search) > -1 ||
          item.Item.indexOf(this.search) > -1
        );
      });
    }
  },
  methods: {
    openModel(isnew, item) {
      this.isnew = isnew;
      if (this.isnew === true) {
        this.tempData = {};
        this.$("#dataModal").modal("show");
      } else if (this.isnew === false) {
        this.tempData = Object.assign({}, item);
        this.$("#dataModal").modal("show");
      } else {
        this.tempData = Object.assign({}, item);
        this.$("#deleteModal").modal("show");
      }
    },
    getData() {
      this.excel = [];
      this.excel_data = [];
      this.$http
        .get("https://stock-5383a.firebaseio.com/stock.json")
        .then(res => {
          this.excel = res.data;
          Object.keys(this.excel).forEach(key => {
            this.excel_data.push({ ...this.excel[key], uid: key });
          });
        });
    },
    updataData() {
      this.$http
        .patch(
          `https://stock-5383a.firebaseio.com/stock/${this.tempData.uid}.json`,
          this.tempData
        )
        .then(() => {
          this.$("#dataModal").modal("hide");
          this.getData();
        });
    },
    deleteData(id) {
      this.$http
        .delete(`https://stock-5383a.firebaseio.com/stock/${id}.json`)
        .then(() => {
          this.$("#deleteModal").modal("hide");
          this.getData();
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

